import classes from './App.module.css';
import AppsIcon from '@mui/icons-material/Apps';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import CloseIcon from '@mui/icons-material/Close';
import {useState, Fragment} from "react";
import AnlTable from "./components/table/AnlTable";
import {DateValue} from "./components/func"

const arr_analysis = [
]

function App() {
    const [anl, getAnl] = useState("")
    const [env, getEnv] = useState("null")
    const [isListOn, getisListOn] = useState(false)
    const [isProd, getIsProd] = useState(false)
    let x = 0

    const add_analysis_handler = (id, anlID, link, env) => {
        arr_analysis.unshift({
            id: id,
            analysisId: anlID,
            date: DateValue(),
            link: link,
            environment: env
        })
    }
    const change_analysis_handler = (analysis, environment) => {
        console.log(analysis, environment)
        getAnl(analysis)
        getEnv(environment)
        getisListOn(false)
    }
    const case_noprod = () => {
        const url = `https://lisa2-${env}.cpas.cz/?state=roboAdvisoryClientAnalysisId%3D${anl}%26action%3DcreateLife`
        openInNewTab(url)
    }
    const case_prod = () => {
        const url = `https://lisa2.generaliceska.cz/?state=roboAdvisoryClientAnalysisId%3D${anl}%26action%3DcreateLife`
        openInNewTab(url)
    }
    const click_handler = () => {
isProd? case_prod():case_noprod()
        //add_analysis_handler(x, anl, url, env)
       // x++
    }

    function openInNewTab(x) {
        window.open(x).focus();
    }

    const updateAnl = (event) => {
        getAnl(event.target.value)
    }
    const updateEnv = (event) => {
        getEnv(event.target.value)
        event.target.value === "PROD"? getIsProd(true):getIsProd(false)
    }
    const listHandler = () => {
        isListOn ? getisListOn(false) : getisListOn(true)
    }
    return (
        <Fragment>
            <div className={classes.App}>
                <div className={`${classes.ContTxt} ${isListOn? classes.opa: ""}`}>
                    <input type={"text"} className={classes.txt} value={anl} onChange={updateAnl} disabled={isListOn ? true : false}/>
                    <select className={classes.combac} defaultValue={env} onChange={updateEnv} disabled={isListOn ? true : false}>
                        <option key={"0"} value={"null"}></option>
                        <option key={"1"} value={"AT"}>AT</option>
                        <option key={"2"} value={"ST"}>ST</option>
                        <option key={"3"} value={"AT2"}>AT2</option>
                        <option key={"4"} value={"ST2"}>ST2</option>
                        <option key={"5"} value={"PROD"}>PROD</option>
                    </select>
                    <button className={classes.hamb} onClick={listHandler} disabled={isListOn ? true : true}>
                        <AppsIcon/>
                    </button>
                </div>
                <button className={`${classes.button} ${isListOn? classes.opa: ""}`} onClick={click_handler}
                        disabled={env === "null" ? true : false || isListOn ? true : false}>generate
                    <NavigateNextIcon/>
                </button>
            </div>
            <div className={`${classes.divlist} ${isListOn ? classes.listOn : classes.listOff}`}>
                <div className={`${classes.divSec} ${classes.secexit} ${isListOn ? "" : classes.seeNothing}`}>
                    <h4>Recently created</h4>
                    <button onClick={listHandler}><CloseIcon/></button>
                </div>
                <div className={`${classes.tablesec} ${isListOn ? "" : classes.seeNothing}`}>
                    <AnlTable changeHandler={change_analysis_handler} arr_analysis={arr_analysis}/>
                </div>
            </div>
        </Fragment>
    );
}

export default App;
