import classes from "./Row.module.css"
import TableCell from "@mui/material/TableCell";
import TableRow from "@mui/material/TableRow";

const Row = (props) => {

    const copy_handler = () =>{
        props.copy_value(props.analysisId, props.environment)
        //console.log(props.analysisId)
        //debugger
    }

    return (<TableRow
        key={props.id}
        sx={{'&:last-child td, &:last-child th': {border: 0}}}>
        <TableCell component="th" scope="row">
            {props.analysisId}
        </TableCell>
        <TableCell align="center">{props.date}</TableCell>
        <TableCell align="center"><b>{props.environment}</b></TableCell>
        <TableCell align="right">
            <button className={classes.bt} onClick={copy_handler}>Copy</button>
        </TableCell>
    </TableRow>)
}
export default Row