
import {Fragment} from "react";
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Row from "../row/Row";

const AnlTable = (props) => {

    const copy_value = (x,y) =>{
        props.changeHandler(x,y)
    }

    return <Fragment>
        <TableContainer component={Paper}>
            <Table sx={{ minWidth: 650 }} aria-label="simple table">
                <TableHead>
                    <TableRow>
                        <TableCell size="small"><b>AnalysisId</b></TableCell>
                        <TableCell size="small" align="center">Created</TableCell>
                        <TableCell size="small" align="center">Environment</TableCell>

                        <TableCell size="small" align="right"></TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {props.arr_analysis.map(x => {
                        return <Row id={x.id} analysisId={x.analysisId} date={x.date} link={x.link} environment={x.environment} copy_value={copy_value}/>
                    })}

                </TableBody>
            </Table>
        </TableContainer>
    </Fragment>
}

export default AnlTable